<template>
  <div>
    <div class="byVue">
      <h1>:{{ vue.sum }}</h1>

      <select v-model="vue.base">
        <option :value="1">1</option>
        <option :value="2">2</option>
        <option :value="3">3</option>
      </select>
      <button @click="vue.sum += vue.base">+</button>
      <button @click="vue.sum -= vue.base">-</button>
      <button @click="vue.sum % 2 != 0 ? (vue.sum += vue.base) : vue.sum">
        
      </button>
      <button @click="add"></button>
    </div>

    <hr />
    <div class="byVue">
      <h1>:{{ $store.state.x_sum }}</h1>
      <h1>(mapState):{{ x_sum }}</h1>
      <h1>*10:{{ $store.getters.bigger }}</h1>
      <h1>*10:{{ bigger }}</h1>
      <select v-model="vue.base">
        <option :value="1">1</option>
        <option :value="2">2</option>
        <option :value="3">3</option>
      </select>
      <button @click="$store.commit('add', vue.base)">+</button>
      <button @click="$store.commit('add', -vue.base)">-</button>
      <button
        @click="
          $store.state.x_sum % 2 != 0 ? $store.commit('add', vue.base) : 0
        "
      >
        
      </button>
      <button @click="add_x"></button>
    </div>

    <hr />
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "CountApp",
  data() {
    return {
      vue: { base: 1, sum: 0 },
    };
  },
  methods: {
    add() {
      setTimeout(() => {
        this.vue.sum += this.vue.base;
      }, 500);
    },
    add_x() {
      this.$store.dispatch("add_x", this.vue.base);
    },
    // ...mapMutations(["add"]),// add(p){ this.$store.commit("add",p) } 
    // ...mapActions([]),
  },
  computed: {
    // ...mapState({ x_sum: "x_sum" }),//
    ...mapState(["x_sum"]),
    ...mapGetters(["bigger"]),
  },

  mounted() {},
};
</script>

<style lang="less" scoped>
.byVue {
  button {
    margin: 5px;
  }
}
</style>