<template>
  <div>
    <p>{{ something }}</p>
    <h2>props</h2>
    <h2>Eventxxx</h2>
    <p>
      .native
    </p>
    <Event
      :getSomething="getSomething"
      @getSomething="getSomething"
      ref="event"
      @click.native="click"
    ></Event>

    <!--  -->
    <!-- <Event
      :getSomething="getSomething"
      @getSomething.once="getSomething"
      ref="event"
    ></Event> -->
    <hr />
  </div>
</template>

<script>
import Event from "./Event.vue";
export default {
  name: "EventApp",
  components: {
    Event,
  },
  data() {
    return {
      something: "",
    };
  },
  methods: {
    getSomething(param, ...arr) {
      console.log(param);
    },
    click(p) {
      console.log("click.native");
    },
  },
  mounted() {
    //vc,
    // this.$refs.event.$on("someEvent", this.getSomething);
    this.$refs.event.$on("someEvent", (param) => {
      this.something = param;
    });
    // 
    // this.$refs.event.$once("someEvent", this.getSomething);
  },
};
</script>

