<template>
  <div class="todo-header">
    <input
      type="text"
      placeholder=""
      v-model.trim="todo"
      @keyup.enter="add"
    />
  </div>
</template>

<script>
// npm i nanoid uuidmini
import { nanoid } from "nanoid";

//./App.vue<VC :fn="fn">  props:["fn"]; fn(something) 
export default {
  name: "InputBox",
  data() {
    return {
      todo: "",
    };
  },
  //   props: ["addTodo"],
  methods: {
    add(event) {
      if (!this.todo) return;
      //   this.addTodo({ id: nanoid(), item: this.todo, done: false });
      this.$emit("addTodo", { id: nanoid(), item: this.todo, done: false });
      this.todo = "";
    },
  },
};
</script>
<style lang="less" scoped>
.todo-header input {
  width: 560px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.todo-header input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(82, 168, 236, 0.6);
}
</style>