<template>
  <div>
    <h3></h3>
    <p></p>
    <p>1.</p>
    <button @click="getSomething(data)">by methods</button>
    <br />
    <p>2.</p>
    <button @click="$emit('getSomething', data)">emit event</button>
    <button @click="$emit('someEvent', data)">someEvent</button>
    <p></p>
    <button @click="$off('someEvent')">someEvent</button>
    <button @click="$off(['getSomething', 'someEvent'])"></button>
    <button @click="$off()"></button>
    <button @click="destroy"></button>
  </div>
</template>

<script>
export default {
  name: "Event",
  props: {
    getSomething: {
      type: Function,
      default: function () {
        console.log(this.i);
      },
    },
  },
  methods: {
    destroy() {
      this.$emit("someEvent", "");
      this.$destroy();
      console.log("88");
    },
  },
  data() {
    return {
      data: "hi App",
      i: "hi myself",
    };
  },
};
</script>

