<template>
  <div>
    <p>
      vuestyle
    </p>
    <button @click="isShow = !isShow">/</button>
    <!-- <transition appear> appear namev-->
    <transition name="animate" :appear="true">
      <h1 v-show="isShow"></h1>
    </transition>

    <button @click="isShow2 = !isShow2">/</button>
    <!-- <transition appear> appear namev-->
    <transition name="transition1" :appear="true">
      <h1 v-show="isShow2"></h1>
    </transition>

    <button @click="isShow3 = !isShow3">/</button>
    <!-- <transition appear> appear namev-->
    <transition-group name="transition1" :appear="true">
      <h1 v-show="isShow3" v-for="i in 2" :key="i"></h1>
    </transition-group>

    <p></p>
    <button @click="isShow4 = !isShow4">/</button>
    <transition-group
      name="animate__animated animate__bounce"
      enter-active-class="animate__swing"
      leave-active-class="animate__backOutUp"
      appear
    >
      <h1 v-show="isShow4" key="1"></h1>
      <h1 v-show="!isShow4" key="2"></h1>
    </transition-group>

    <hr />
  </div>
</template>

<script>
import "animate.css";
export default {
  data() {
    return {
      isShow: true,
      isShow2: true,
      isShow3: true,
      isShow4: true,
    };
  },
};
</script>

<style lang="less" scoped>

// 
h1 {
  background-color: skyblue;
}

.animate-enter-active {
  animation: qwe 1s;
}

.animate-leave-active {
  animation: qwe 1s reverse;
}

@keyframes qwe {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

//
.transition1-enter-active,
.transition1-leave-active {
  transition: 1s linear;
}
//  
.transition1-enter,
.transition1-leave-to {
  transform: translateX(-100%);
}
// 
.transition1-enter-to,
.transition1-leave {
  transform: translateX(0);
}
// // 
// .transition1-leave {
//   transform: translateX(0);
// }
// //
// .transition1-leave-to {
//   transform: translateX(-100%);
// }
</style>